import { firestore } from "../firebase"
import getUID from "./getUID"

export const findChatURL = async (allParticipants, currentUser, setShouldCreate, setFriendChatId) => {
	let foundChat = false
	let chatId = 0
	if (currentUser.chats) {
		// const chatInfo = (await firestore.collection('chats').where('participants', "==", [friendId, currentUser.uid]).get())
		// const chatInfo2 = (await firestore.collection('chats').where('participants', "==", [currentUser.uid, friendId]).get())
		// chatInfo.docs.forEach(chat => {
		// 	foundChat = true
		// 	setShouldCreate(false)
		// 	setFriendChatId(chat.data().id)
		// 	chatId = chat.data().id
		// })
		// chatInfo2.docs.forEach(chat => {
		// 	foundChat = true
		// 	setShouldCreate(false)
		// 	setFriendChatId(chat.data().id)
		// 	chatId = chat.data().id
		// })

		// if (!foundChat) {
		// 	const random = getUID()
		// 	foundChat = true
		// 	setShouldCreate(true)
		// 	setFriendChatId(random)
		// 	chatId = random
		// }

		//   :    ,    .     .      
		//   ,       ,   count 1,  ,     
		//     ,    + 1 (        ) 
		//             ,    
		const chatsWhereUserIn = await firestore.collection('chats').where('participants', 'array-contains', currentUser.uid).get()
		chatsWhereUserIn.docs.forEach(chat=>{
			let count = 0
			allParticipants.every(personId=>{
				if(chat.data().participants.includes(personId)){
					count++
					return true
				}
				else return false
			})			
			if(count + 1 === chat.data().participants.length && count === allParticipants.length){
				foundChat = true
				setShouldCreate(false)
				setFriendChatId(chat.data().id)
				chatId = chat.data().id
				return false
			}
			else return true
		})

		if (!foundChat) {
			const random = getUID()
			foundChat = true
			setShouldCreate(true)
			setFriendChatId(random)
			chatId = random
		}
		return chatId
	}
}